button {
  transition: all 0.3s ease;
}

button:hover {
  background-color: #c4cf24;
  transform: scale(1.05);
}

.success-message {
  color: green;
  animation: fadeIn 0.5s ease-in;
}

.error-message {
  color: red;
  animation: fadeIn 0.5s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
